var _a;
import { MenuSounds } from './components/Audio';
import Game from './components/Game';
import { preloadSounds } from './utils/sound';
var SLIDE_CONFIG = (_a = {},
    _a["left" /* LEFT */] = {
        title: document.querySelector(".slider-" + "left" /* LEFT */ + " .slider-title"),
        data: document.querySelector(".slider-" + "left" /* LEFT */ + " .slider-data"),
    },
    _a["right" /* RIGHT */] = {
        title: document.querySelector(".slider-" + "right" /* RIGHT */ + " .slider-title"),
        data: document.querySelector(".slider-" + "right" /* RIGHT */ + " .slider-data"),
    },
    _a);
var snowBlock = document.querySelector('.snow');
var checkbox = document.querySelector('#checkbox');
var playButton = document.querySelector('#play');
var mainBlock = document.querySelector('.menu');
var hideMainBlock = function () {
    mainBlock.classList.add('fadeOut');
    setTimeout(function () { return mainBlock.classList.add('none'); }, 700);
};
var snowHandler = function () {
    if (snowBlock.style.display === 'block')
        snowBlock.style.display = 'none';
    else
        snowBlock.style.display = 'block';
};
var setSlides = function (side, isHovered) {
    var value = isHovered ? '50px' : 'calc(-40vw - 70px)';
    var direction = isHovered ? 'over' : 'out';
    var mouseMoveHandler = function () {
        SLIDE_CONFIG[side].data.style[side] = value;
    };
    SLIDE_CONFIG[side].title.addEventListener("mouse" + direction, mouseMoveHandler);
};
var changeGameMode = function (game, menuSounds) {
    if (checkbox.checked) {
        snowBlock.classList.add('block');
        menuSounds.winterModeSound();
        game.setToWinter();
    }
    else {
        snowBlock.classList.remove('block');
        game.setToVanilla();
    }
};
var initializeSlides = function () {
    setSlides("left" /* LEFT */, true);
    setSlides("left" /* LEFT */, false);
    setSlides("right" /* RIGHT */, true);
    setSlides("right" /* RIGHT */, false);
};
var init = function () {
    var game = new Game();
    var menuSounds = new MenuSounds();
    var checkboxHandler = function () {
        snowHandler();
        changeGameMode(game, menuSounds);
    };
    var gameHandler = function () {
        hideMainBlock();
        MenuSounds.disableMenuSounds();
        game.loadGame();
    };
    checkbox.addEventListener('change', checkboxHandler);
    playButton.addEventListener('click', gameHandler);
    initializeSlides();
    preloadSounds();
    menuSounds.enableMenuSounds();
    game.setToVanilla();
};
document.addEventListener('DOMContentLoaded', init);
